#h5p-more-filters select {
  width: 100%;
  max-width: 100%;
  min-height: 44px;
  padding: 2px 8px;
  background-color: hsl(0deg, 0%, 100%);
  border-color: hsl(0deg, 0%, 80%);
  border-radius: 4px;
  border-style: solid;
  border-width: 1px;
}
#h5p-more-filters select option[name=placeholder] {
  color: hsl(0deg, 0%, 50%);
}
#h5p-more-filters select option:not([name=placeholder]) {
  color: hsl(0deg, 0%, 20%);
}